@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  height: 100%;
}

body {
  display: flex;
  flex-direction: column;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: "PT Sans", "Arial", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  color: #333333;
  background: #f2f2f2;
}

img {
  max-width: 100%;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  margin: 0 auto;
  max-width: 768px;
  height: auto;
}

@media (min-width: 768px) {
  .container {
    max-width: 1200px;
  }
}
@media (min-width: 1200px) {
  .container {
    box-shadow: 0 -5px 9px 2px #dbdbdb;
  }
}
.page-header {
  position: relative;
}

.page-main {
  background-color: #ffffff;
}

.page-main__basic-section {
  margin: 0 auto;
}

.menu--closed {
  min-height: 56px;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .menu--closed {
    min-height: inherit;
  }
}

.menu__button {
  position: absolute;
  top: 17px;
  right: 20px;
  width: 22px;
  height: 22px;
  background-color: transparent;
  border: 0;
  outline: 0;
}
.menu__button:hover {
  opacity: 0.6;
}
.menu__button:active {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .menu__button {
    display: none;
  }
}

.menu--opened .menu__button::before, .menu--opened .menu__button::after {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  content: "";
  height: 28px;
  width: 4px;
  background-color: #ffffff;
}
.menu--opened .menu__button::before {
  transform: rotate(45deg);
}
.menu--opened .menu__button::after {
  transform: rotate(-45deg);
}

.menu--closed .menu__button {
  border-top: 4px solid #81b3d2;
}
.menu--closed .menu__button::before {
  position: absolute;
  content: "";
  top: 5px;
  left: 0;
  width: 100%;
  height: 4px;
  background-color: #81b3d2;
}
.menu--closed .menu__button::after {
  position: absolute;
  content: "";
  bottom: 0;
  left: 0;
  width: 100%;
  height: 4px;
  background-color: #81b3d2;
}

.menu--nojs .menu__button {
  display: none;
}

.menu--closed .menu__list {
  display: none;
}

.menu--opened .menu__list {
  display: block;
}

.menu--nojs .menu__toggle {
  display: none;
}

.menu--nojs .menu__list {
  display: block;
}

.menu .menu__list {
  margin: 0;
  padding: 0;
  list-style: none;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .menu .menu__list {
    display: flex;
    font-size: 14px;
    line-height: 21px;
    background-color: #ffffff;
  }
}

.menu__item {
  background-color: #81b3d2;
  box-shadow: 0 -1px 0 0 rgba(1, 1, 1, 0.2) inset, inset 0 1px 0 rgba(255, 255, 255, 0.2);
}
.menu__item:last-child {
  box-shadow: 0 -14px 15px -15px rgba(0, 0, 0, 0.4) inset;
}
.menu__item:hover {
  background-color: #669ec0;
}
.menu__item:active {
  background-color: #5496bd;
}
.menu__item:active .menu__link {
  opacity: 0.3;
}
@media (min-width: 768px) {
  .menu__item {
    width: 20%;
    background-color: #ffffff;
    box-shadow: none;
  }
  .menu__item:last-child {
    box-shadow: none;
  }
  .menu__item:hover, .menu__item:active {
    background-color: transparent;
  }
  .menu__item:nth-child(3) {
    margin-left: auto;
  }
}
@media (min-width: 1200px) {
  .menu__item {
    width: 240px;
  }
}

.menu__link {
  display: block;
  padding-top: 17px;
  padding-bottom: 12px;
  color: #ffffff;
  text-decoration: none;
  text-align: center;
}
@media (min-width: 768px) {
  .menu__link {
    color: #000000;
  }
  .menu__link:hover {
    color: #81b3d2;
  }
  .menu__link:active {
    opacity: 0.3;
  }
  .menu__link--current {
    color: #766357;
  }
}

.menu__logo {
  position: absolute;
  width: fit-content;
  height: fit-content;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: 0 auto;
  z-index: 1;
}
.menu__logo--opened {
  display: block;
}
.menu__logo--closed {
  display: none;
}

.menu--nojs .menu__logo {
  position: relative;
}
@media (min-width: 768px) {
  .menu--nojs .menu__logo {
    position: absolute;
  }
}

.welcome {
  position: relative;
}
.welcome--index {
  padding: 90px 19px 85px;
  background-image: url("../img/backgrounds/bg-welcome-mobile.jpg");
  background-color: #81b3d2;
  background-position: center bottom;
  background-repeat: no-repeat;
  background-size: cover;
}
.welcome--index::after {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
  content: "";
  width: auto;
  height: 57px;
  background-image: url("../img/backgrounds/bg-welcome-bottom-dekstop.svg");
  background-position: center bottom;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (min-width: 768px) {
  .welcome--index {
    background-image: url("../img/backgrounds/bg-welcome-dekstop.jpg");
  }
}

.welcome__image {
  display: block;
  margin: 0 auto;
}

.basic-section {
  padding: 32px 20px;
  display: grid;
  gap: 32px;
}
.basic-section--video {
  padding: 17px 20px 0;
  position: relative;
}
@media (max-width: 767px) {
  .basic-section--video::after {
    position: absolute;
    top: 0;
    content: "";
    width: calc(100% + 40px);
    height: 1px;
    margin-left: -20px;
    background-color: #e5e5e5;
  }
}
@media (min-width: 768px) {
  .basic-section {
    padding: 47px 0 55px;
    max-width: 520px;
    gap: 45px;
    justify-items: center;
  }
  .basic-section--opposite {
    max-width: 458px;
    padding-top: 61px;
    padding-bottom: 58px;
  }
  .basic-section--opposite .basic-section__title {
    font-size: 30px;
  }
  .basic-section--video {
    max-width: none;
    padding: 57px 60px 40px;
  }
  .basic-section--video .basic-section__title {
    max-width: 445px;
    font-size: 30px;
  }
  .basic-section--video .basic-section__desc {
    max-width: 445px;
  }
}

.basic-section__title {
  margin: 0;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .basic-section__title {
    font-size: 21px;
  }
}

.basic-section__desc {
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-align: center;
  text-transform: uppercase;
}

.basic-section__image {
  display: block;
  object-fit: cover;
  min-height: 500px;
}

.advantages__list {
  margin: 0;
  padding: 0;
  list-style: none;
  counter-reset: number;
}
@media (min-width: 768px) {
  .advantages__list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
}

.advantage {
  background-color: #eeeeee;
}
.advantage:first-child {
  padding-top: 46px;
  background-image: url("../img/backgrounds/bg-advatages-top-mobile.svg");
  background-position: top center;
  background-repeat: no-repeat;
  background-size: contain;
}
@media (min-width: 768px) {
  .advantage:first-child {
    padding-top: 0;
    background-image: none;
  }
}
.advantage--secondary {
  padding-bottom: 0;
  color: #ffffff;
  background-color: #81b3d2;
}
@media (min-width: 768px) {
  .advantage--secondary {
    grid-column: 3 span;
    display: grid;
    grid-template-columns: 1fr 2fr;
  }
  .advantage--opposite {
    grid-template-columns: 2fr 1fr;
  }
}

.advantage__wrap {
  padding: 26px 20px;
}
@media (min-width: 768px) {
  .advantage__wrap {
    min-height: 256px;
    display: flex;
    align-items: center;
    flex-direction: column;
    padding: 40px 32px 44px;
  }
}

.advantage__title {
  margin-top: 0;
  margin-bottom: 11px;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-align: center;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .advantage__title {
    font-size: 21px;
  }
}

.advantage__number {
  margin-top: 0;
  margin-bottom: 17px;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-align: center;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .advantage__number {
    margin-bottom: auto;
  }
}

.advantage__desc {
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-align: center;
  text-transform: uppercase;
}

.advantage__image {
  display: block;
  object-fit: cover;
  width: 100%;
}
@media (min-width: 768px) {
  .advantage__image--order {
    order: -1;
  }
}

.advantage__number-count::before {
  counter-increment: number;
  content: counter(number);
}

.features {
  margin: 0;
  padding: 0;
  list-style: none;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .features {
    grid-column: 1/3;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 70px;
  }
}

.features__wrap {
  padding: 23px 20px 52px;
}
@media (min-width: 768px) {
  .features__wrap {
    padding: 63px 32px 59px;
  }
}

.feature {
  position: relative;
  padding-top: 8px;
  padding-left: 47px;
}
.feature:not(:last-child) {
  margin-bottom: 23px;
}
.feature::before {
  position: absolute;
  top: 0;
  left: 0;
  width: 40px;
  height: 40px;
  content: "";
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
}
@media (min-width: 768px) {
  .feature::before {
    width: 75px;
    height: 75px;
    top: 0;
    left: 0;
    right: 0;
    margin: auto;
  }
}
.feature--home::before {
  background-image: url("../img/house.svg");
}
.feature--burger::before {
  background-image: url("../img/burger.svg");
}
.feature--gift::before {
  background-image: url("../img/present.svg");
}
@media (min-width: 768px) {
  .feature {
    padding-top: 98px;
    padding-left: 0;
  }
}

.feature__title {
  margin-top: 0;
  margin-bottom: 16px;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
  color: #000000;
}
@media (min-width: 768px) {
  .feature__title {
    font-size: 21px;
    text-align: center;
  }
}

.feature__desc {
  margin-top: 0;
  margin-bottom: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
  color: #000000;
}
@media (min-width: 768px) {
  .feature__desc {
    text-align: center;
  }
}

.page-footer {
  padding: 38px 20px 23px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .page-footer {
    padding: 36px 60px;
    flex-direction: row;
    justify-content: space-between;
  }
}
@media (min-width: 1200px) {
  .page-footer {
    padding: 36px 120px;
  }
}

.page-footer__hashtag {
  margin-top: 0;
  margin-bottom: 34px;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .page-footer__hashtag {
    margin-bottom: 0;
  }
}

.page-footer__social {
  margin-top: 0;
  margin-bottom: 40px;
}
@media (min-width: 768px) {
  .page-footer__social {
    margin-bottom: 0;
  }
}

.social {
  margin: 0;
  padding: 0;
  list-style: none;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 6px;
}

.social__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 48px;
  min-height: 48px;
  background-color: #81b3d2;
}
.social__link:hover {
  background-color: #669ec0;
}
.social__link:active {
  background-color: #5496bd;
}
.social__link:active .social__icon {
  opacity: 0.3;
}

.social__icon {
  fill: #ffffff;
}

.copyright__text {
  display: block;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
}

.copyright__link {
  display: grid;
  grid-template-columns: min-content min-content;
  justify-content: center;
  align-items: center;
  gap: 12px;
  color: #000000;
  text-decoration: none;
}
.copyright__link:hover {
  color: #81b3d2;
  fill: #81b3d2;
}
.copyright__link:active {
  color: #b3b3b3;
  fill: #b3b3b3;
}

.button {
  display: block;
  padding-top: 12px;
  padding-bottom: 12px;
  font-weight: 700;
  font-size: 21px;
  line-height: 26px;
  text-transform: uppercase;
  text-align: center;
  cursor: pointer;
  color: #ffffff;
  text-decoration: none;
  background-color: #81b3d2;
}
.button:hover {
  background-color: #669ec0;
}
.button:active {
  background-color: #5496bd;
  color: rgba(255, 255, 255, 0.3);
}
.button:disabled {
  background-color: #e5e5e5;
}
.button--form {
  width: 100%;
  margin-bottom: 3px;
  border: none;
}
@media (min-width: 768px) {
  .button--section {
    width: 458px;
  }
}

.map {
  min-height: 350px;
}

.map__image {
  display: block;
  object-fit: cover;
  width: 100%;
  height: 350px;
}

.posts {
  padding-left: 20px;
  padding-right: 20px;
}
@media (min-width: 768px) {
  .posts {
    padding-left: 60px;
    padding-right: 60px;
  }
}

.posts__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
@media (min-width: 768px) {
  .posts__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}
@media (min-width: 1200px) {
  .posts__list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
  }
}

.post {
  position: relative;
  padding-top: 24px;
  padding-bottom: 25px;
}
@media (max-width: 767px) {
  .post::after {
    position: absolute;
    top: 0;
    content: "";
    width: calc(100% + 40px);
    height: 1px;
    margin-left: -20px;
    background-color: #e5e5e5;
  }
}
@media (min-width: 768px) {
  .post {
    padding-top: 0;
    padding-bottom: 0;
  }
  .post:first-child {
    grid-column: 1/3;
  }
}
@media (min-width: 1200px) {
  .post {
    position: relative;
  }
  .post:first-child {
    grid-column: 1/4;
  }
}

@media (min-width: 768px) {
  .post__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 22px 20px 15px;
    background-color: #f2f2f2;
  }
  .post__wrap--color {
    background-color: #eaeaea;
  }
}
@media (min-width: 1200px) {
  .post__wrap {
    position: absolute;
    bottom: 0;
    z-index: 1;
    width: 100%;
    background-color: rgba(0, 0, 0, 0.3);
    color: #ffffff;
  }
}

.post__image {
  width: 100%;
  display: block;
  min-height: 280px;
  object-fit: cover;
  margin-bottom: 15px;
}
@media (min-width: 768px) {
  .post__image {
    min-height: 267px;
    margin-bottom: 0;
  }
  .post__image--first {
    min-height: 400px;
  }
}

.post__desc {
  margin-bottom: 14px;
}
@media (min-width: 768px) {
  .post__desc {
    margin-bottom: 0;
  }
}

.post__title {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 14px;
  line-height: 21px;
  font-weight: 700;
  text-transform: uppercase;
}

.post__author {
  margin-top: 0;
  margin-bottom: 0;
  font-size: 14px;
  line-height: 21px;
  font-weight: 400;
}

.post__likes {
  display: grid;
  grid-template-columns: min-content min-content;
  gap: 14px;
}

.post__likes-icon-wrapper {
  display: block;
  line-height: 0;
}

.post__likes-icon {
  fill: #b2b2b2;
  width: 23px;
  height: 21px;
}
@media (min-width: 1200px) {
  .post__likes-icon {
    fill: #ffffff;
  }
}

.post__likes-count {
  font-size: 14px;
  line-height: 21px;
  font-weight: 700;
  display: block;
}

.form {
  padding: 0 20px;
}
@media (min-width: 768px) {
  .form {
    padding: 0 60px;
  }
}

.form__section-wrap {
  position: relative;
}
@media (min-width: 768px) {
  .form__section-wrap {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 88px;
  }
  .form__section-wrap::before {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    width: 1px;
    height: 100%;
    background-color: #dbdbdb;
  }
}

.form__section {
  position: relative;
  margin: 0;
  padding: 24px 0 39px;
  border: none;
}
@media (max-width: 767px) {
  .form__section::after {
    position: absolute;
    top: 0;
    left: 0;
    content: "";
    width: calc(100% + 40px);
    height: 1px;
    margin-left: -20px;
    background-color: #e5e5e5;
  }
}
@media (min-width: 768px) {
  .form__section--line::before {
    position: absolute;
    left: 0;
    top: 36px;
    right: 0;
    bottom: 0;
    align-self: center;
    width: 100%;
    height: 1px;
    background-color: #d5d5d5;
    content: "";
  }
}

.form__legend {
  position: relative;
  margin: 0 auto 20px;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
  text-align: center;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .form__legend {
    font-size: 21px;
    margin-bottom: 28px;
  }
  .form__legend--line {
    max-width: 400px;
    padding-left: 50px;
    padding-right: 50px;
  }
}

.form__field-wrap {
  display: flex;
  flex-direction: column;
}
.form__field-wrap:not(:last-child) {
  margin-bottom: 16px;
}

@media (min-width: 768px) {
  .form__choice-list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 16px;
  }
  .form__choice-list--checkbox {
    padding: 0 84px;
  }
}
@media (min-width: 1200px) {
  .form__choice-list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 28px;
  }
  .form__choice-list--checkbox {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 64px;
    padding: 0;
  }
}

.form__choice-wrap:not(:last-child) {
  margin-bottom: 16px;
}
@media (min-width: 768px) {
  .form__choice-wrap:not(:last-child) {
    margin-bottom: 0;
  }
}

.form__label {
  display: block;
  margin-left: 16px;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
}
.form__label--radio {
  position: relative;
  margin-left: 0;
  padding: 10px 0 10px 62px;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
}
.form__label--radio::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 46px;
  height: 46px;
  background-color: #f2f2f2;
  border: 15px solid #f2f2f2;
  border-radius: 50%;
}
.form__label--radio:hover::before {
  background-color: #e5e5e5;
  border: 15px solid #e5e5e5;
}
.form__label--radio:active::before {
  background: rgba(51, 51, 51, 0.3);
  border: 15px solid #d5d5d5;
}
.form__label--checkbox {
  position: relative;
  margin-left: 0;
  padding: 10px 0 10px 62px;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
}
.form__label--checkbox::before {
  content: "";
  position: absolute;
  left: 0;
  top: 0;
  width: 46px;
  height: 46px;
  background-color: #f2f2f2;
  border-radius: 4px;
}
.form__label--checkbox:hover::before {
  background-color: #e5e5e5;
}
.form__label--checkbox:active::before {
  background-color: #d5d5d5;
}

.form__field {
  display: block;
  width: 100%;
  padding: 16px 58px 16px 16px;
  margin: 0;
  border: none;
  background-color: #f2f2f2;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: #000000;
  outline: none;
  font-family: inherit;
}
.form__field:hover {
  background-color: #e5e5e5;
}
.form__field:focus, .form__field:focus-visible {
  background-color: #ffffff;
  box-shadow: inset 0 0 0 4px #e5e5e5;
}
.form__field:focus::placeholder, .form__field:focus-visible::placeholder {
  color: transparent;
}
.form__field::placeholder {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  color: #000000;
}
.form__field--email, .form__field--phone {
  position: relative;
}
.form__field--email::placeholder, .form__field--phone::placeholder {
  color: rgba(0, 0, 0, 0.3);
}

.form__field-icon-wrap {
  position: relative;
}
.form__field-icon-wrap .form__field-icon {
  position: absolute;
  top: 0;
  right: 0;
  width: 58px;
  height: 58px;
  background-color: #e6e6e6;
  background-position: center;
  background-repeat: no-repeat;
}
.form__field-icon-wrap .form__field-icon--email {
  background-image: url("../img/email.svg");
}
.form__field-icon-wrap .form__field-icon--phone {
  background-image: url("../img/phone.svg");
}

.form__radio,
.form__checkbox {
  display: none;
}

.form__radio:checked + .form__label--radio::before {
  background-color: #000000;
}

.form__radio:checked + .form__label--radio:active::before {
  background-color: rgba(51, 51, 51, 0.3);
}

.form__checkbox:checked + .form__label--checkbox::before {
  background-image: url("../img/tick.svg");
  background-position: center;
  background-repeat: no-repeat;
}

.form__checkbox:checked + .form__label--checkbox:active::before {
  opacity: 0.3;
}

.form__textbox {
  min-height: 150px;
  width: 100%;
  margin: 0;
  padding: 16px;
  border: none;
  border-radius: 0;
  background-color: #f2f2f2;
  color: #000000;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  resize: none;
  outline: none;
  font-family: inherit;
}
.form__textbox::placeholder {
  color: rgba(0, 0, 0, 0.3);
}
.form__textbox:hover {
  background-color: #e5e5e5;
}
.form__textbox:focus, .form__textbox:focus-visible {
  background-color: #ffffff;
  box-shadow: inset 0 0 0 4px #e5e5e5;
}
.form__textbox:focus::placeholder, .form__textbox:focus-visible::placeholder {
  color: transparent;
}

.form__button-wrap {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.form__send-mark {
  font-weight: 400;
  font-size: 12px;
  line-height: 21px;
  text-transform: uppercase;
  color: rgba(51, 51, 51, 0.6);
}

.popup {
  background: #ffffff;
  width: 320px;
  position: relative;
  margin: auto;
}
.popup--success {
  display: none;
}
.popup--fail {
  display: none;
}
@media (min-width: 768px) {
  .popup--success {
    width: 640px;
  }
  .popup--fail {
    width: 400px;
  }
}

.popup__wrap {
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  z-index: 9999;
  display: none;
}

.popup__desc-wrap {
  padding: 45px 41px 30px;
}
@media (min-width: 768px) {
  .popup__desc-wrap {
    padding: 38px 47px 47px;
  }
  .popup__desc-wrap--success {
    padding: 57px 98px 35px;
  }
}

.popup__title {
  margin-bottom: 26px;
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  text-transform: uppercase;
  text-align: center;
}
@media (min-width: 768px) {
  .popup__title {
    font-size: 30px;
    line-height: 40px;
    margin-bottom: 30px;
  }
}

.popup__desc {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
  text-align: center;
}

.popup__button-bg {
  padding: 0 70px 35px;
}
@media (min-width: 768px) {
  .popup__button-bg {
    padding: 0 64px 49px;
  }
}
.popup__button-bg--grey {
  width: 100%;
  padding: 20px;
  background-color: #f2f2f2;
}
@media (min-width: 768px) {
  .popup__button-bg--grey {
    padding: 44px 91px;
  }
}